/*******************************************************************************
 * SD_SoqlEditor Class
 ******************************************************************************/
var SD_SoqlEditor = function(options){
  options = options || {};
  this.metadata = options.metadata;
  this.upperKeyMetaMap = this.getUpperKeyMap(options.metadata);
  this.client = options.client;
  this.sObjectName;
  this.editor = null;
  this.objLabelMap = {};
  this.cacheWordCompleter = {};
  this.filedMetaMap = {};
  this.fieldNameMap = options.fieldNameMap || {};
  this.refShipNameMap = options.refShipNameMap || {};
  this.refToPairMap = options.refToPairMap || {};
  this.childRelShipMap = options.childRelShipMap || {};
}

// Quick SOQL Editor
SD_SoqlEditor.prototype.init = function(options) {
  var self = this;
  var fields = options.fields;
  var values = options.values;
  var sobject = options.sobject;
  
  var oId = options.id || null;
  self.sObjectName = sobject.name;
  // Set childRelationships
  var childRelationships = options.childRelationships || [];
  self.childRelShipMap = {};
  for(var i = 0; i < childRelationships.length; i++) {
    var child = childRelationships[i];
    if(T_Util.isBlank(child.relationshipName) || T_Util.isBlank(child.childSObject)) continue;
    var upperObjectName = child.childSObject.toUpperCase();
    if(!self.upperKeyMetaMap.hasOwnProperty(upperObjectName)) continue;
    var objMeta = self.upperKeyMetaMap[upperObjectName];
    self.childRelShipMap[child.relationshipName.toUpperCase()] = { sobject : child.childSObject, label : objMeta.label };
  }
  // Object Name - Label map
  for(var k in self.metadata){
    var obj = self.metadata[k];
    if(T_Util.isBlank(obj.name) || T_Util.isBlank(obj.label)) continue;
    self.objLabelMap[obj.name.toUpperCase()] = obj.label;
  }
  
  var valuesMap = {};
  for(var i=0; i<values.length; i++){
    var value = values[i];
    if( T_Util.isBlank(value.value) || typeof value.value != 'string' ) continue;
    if( T_Util.isBlank(value.value.trim()) ) continue;
    valuesMap[value.name] = value.value;
  }
  //<span class="badge up badge-danger">New</span>
  var buttonHtml = '<div class="nav-button-panel" id="quick-soql-buttons"><span class="query-result"></span>';
  buttonHtml += '<span class="pull-right btn-group"><a href="#" id="btn-quick-soql-query" class="btn btn-primary btn-xs flat">Query</a>';
  buttonHtml += '&nbsp;&nbsp;<a href="#" id="btn-quick-soql-export" title="Export query results (~2000) to Excel" class="btn btn-primary btn-xs flat" data-container="body" data-toggle="tooltip" data-placement="top">';
  buttonHtml += 'Export</a><a id="btn-quick-soql-export-dropdown" class="btn btn-primary btn-xs flat dropdown-toggle dropdown-toggle-split" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"><span class="sr-only">Dropdown</span></a>';
  buttonHtml += '<div class="dropdown-menu"><a href="#" id="btn-quick-soql-export-all" class="dropdown-item" href="#" title="Export all results to Excel, with splitting into multiple files of maximum 2000 rows."  data-container="body" data-toggle="tooltip" data-placement="top">Export All</a></div></span>';
  buttonHtml += '</div>';

  var filedsHtml = '<div class="nav-field-panel" id="nav-field-panel"><table class="table table-striped">';
  filedsHtml += '<tr class="header"><th class="chk">';
  filedsHtml += '<div class="checkbox checkbox-primary"><input type="checkbox" id="chk-all-fields"><label for="chk-all-fields">&nbsp;</label>Field Name</div></th>';
  filedsHtml += '<th>Data Type</th></tr>'

  self.cacheWordCompleter[sobject.name] = [{ name : sobject.name, label : 'sObject'}]; // 
  var sObjKey = sobject.name.toUpperCase();
  self.filedMetaMap[sObjKey] = fields;
  self.fieldNameMap = {};
  self.fieldNameMap[sObjKey] = {};
  self.refShipNameMap = {};
  self.refShipNameMap[sObjKey] = {};

  // HTML
  var step = 0;
  var rootRelationship = null;
  filedsHtml += self.getFieldRowsHTML(sobject, fields, step, rootRelationship, valuesMap);
  filedsHtml += '</table></div>';

  var editor = '<div id="quick-soql-editor-wrap"><pre id="quick-soql-editor" class="editor" readOnly="true"></pre></div>';
  editor += buttonHtml + filedsHtml;
  // SOQL
  $('#container-quick-soql').empty().append(editor);

  var $editor = self.initEditor('quick-soql-editor', 'sql');
  self.editor = $editor;
  self.setEditorWordCompleter($editor);
  
  var sObjectName = sobject.name;
  var condition = (T_Util.isBlank(oId)) ? '' : 'Id = \'' + oId + '\'';
  self.refreshSoql($editor, sObjectName, condition);
  $('#btn-quick-soql-export,#btn-quick-soql-export-all').tooltip();
  $('#container-quick-soql').find('#chk-all-fields').first().click(function(){
    $('#nav-field-panel').find('.field-row.step0:not(.ref-row) input[type="checkbox"]').prop('checked', $(this).is(':checked'));
    self.refreshSoql($editor, sObjectName);
  });
  $('#container-quick-soql').find('.field-row.step0:not(.ref-row) input[type="checkbox"]').click(function(){
    var checked = $(this).is(':checked');
    var allChecked = true;
    $('#nav-field-panel').find('.field-row.step0:not(.ref-row) input[type="checkbox"]').each(function(){
      if(checked != $(this).is(':checked')){
        allChecked = false;
      }
    });
    $('#container-quick-soql').find('#chk-all-fields').first().prop('checked', allChecked);
    self.refreshSoql($editor, self.sObjectName);
  });

  if(options.callback) options.callback();
}

// Editor
SD_SoqlEditor.prototype.setEditorWordCompleter = function($editor) { 
  var self = this;
  var keywords = ['AND', 'ASC', 'DESC', 'EXCLUDES', 'FIRST', 'FROM','GROUP BY', 'HAVING', 'IN', 
  'INCLUDES', 'LAST', 'LIKE', 'LIMIT', 'NOT', 'NULL', 'NULLS', 'OR', 'SELECT', 'WHERE','WITH', 'OFFSET', 'ORDER BY', 
  'FOR VIEW', 'FOR REFERENCE', 'FOR UPDATE', 'USING SCOPE', 'TYPEOF', 'WHEN', 'THEN', 'ELSE', 'END', 'convertCurrency()', 
  'COUNT()', 'FORMAT()', 'COUNT_DISTINCT()', 'MIN()', 'MAX()', 'SUM()', 
  'CALENDAR_MONTH', 'CALENDAR_QUARTER()', 'CALENDAR_YEAR()', 'DAY_IN_MONTH()', 'DAY_IN_WEEK()', 'DAY_IN_YEAR()', 
  'DAY_ONLY()', 'FISCAL_MONTH()', 'FISCAL_QUARTER()', 'FISCAL_YEAR()', 'HOUR_IN_DAY()', 'WEEK_IN_MONTH()', 'WEEK_IN_YEAR()']; //keywords
  var keyWordCompleter = {
    getCompletions: function(editor, session, pos, prefix, callback) {
      callback(null, keywords.map(function(word) {
        return {
          caption: word,
          value: word,
          meta: "Keyword"
        };
      }));
    }
  }
  var completions = [];
  var sobjects = Object.keys(self.cacheWordCompleter);
  for(var i = 0; i < sobjects.length; i++) {
    completions = completions.concat(self.cacheWordCompleter[sobjects[i]]);
  }
  var staticWordCompleter = {
    getCompletions: function(editor, session, pos, prefix, callback) {
      callback(null, completions.map(function(item) {
        return {
          caption: item.name,
          value: item.name,
          meta: item.label
        };
      }));
    }
  }
  $editor.completers = [staticWordCompleter, keyWordCompleter];
}

// HTML 
SD_SoqlEditor.prototype.getFieldRowsHTML = function(sobject, fields, step, rootRelationship, valuesMap) { 
  var self = this;
  var filedsHtml = '';
  // Data Type 
  var docs = new SD_DocGenerator({ 
    metadata : self.metadata,
    language : self.client.pageInfo.userInfo.language 
  });
  
  var fieldTypesMap = docs.getFieldTypesMap();
  for(var i=0; i < fields.length; i++){
    var field = fields[i];
    var checked = (field.name == 'Id' && step == 0) ? ' checked="checked"' : '';
    valuesMap = valuesMap || {};
    if(valuesMap.hasOwnProperty(field.name)){
      // 
      checked = ' checked="checked"';
    }
    // Data Type
    var fieldTypeLabel = docs.getFieldType(field, fieldTypesMap);
    if(field.type == 'reference' && !T_Util.isBlank(field.referenceTo)) {
      var refProp = docs.getFieldReferenceProp(field, self.objLabelMap);
      fieldTypeLabel += ' ('+refProp.refLabels.join(',')+')';
    }

    rootRelationship = rootRelationship || '';
    //var chKey = 'chk_'+step+'_'+sobject.name+'_'+field.name;
    var fieldApiName = (step == 0) ? field.name : rootRelationship + '.' + field.name;
    var chKey = fieldApiName.replace(/\./g, '_');
    filedsHtml += '<tr class="field-row step'+step + ((i==0&&step==0)?' first':'') + '" data-rootrelationship="'+rootRelationship+'"><td class="chk">';
    filedsHtml += '<div class="checkbox checkbox-primary"><input type="checkbox" '+checked+' value="'+fieldApiName+'" id="'+chKey+'">';
    filedsHtml += '<label for="'+chKey+'">&nbsp;'+field.label+'&nbsp;<br /><small>&nbsp;'+field.name+'</small></label></div>';
    filedsHtml += '</td><td class="type">'+fieldTypeLabel+'</td></tr>';

    if(field.type == 'reference' && !T_Util.isBlank(field.referenceTo) && !T_Util.isBlank(field.relationshipName) && step < 4) {
      //  ID 
      //console.log('>>>>> field: ', field.name, field);
      fieldApiName = (step == 0) ? field.relationshipName : rootRelationship + '.' + field.relationshipName;
      chKey = fieldApiName.replace(/\./g, '_');
      var refLabel = (!T_Util.isTrue(field.custom) && field.label.endsWith(' ID')) ? T_Util.replaceLast(field.label, ' ID', '') : field.label;
      var referenceTo = Array.isArray(field.referenceTo) ? field.referenceTo[0] : field.referenceTo;
      filedsHtml += '<tr class="field-row step'+step+' ref-row uncomplete' + ((i==0)?' first':'') + '" data-sobject="'+referenceTo+ '" data-rootrelationship="'+rootRelationship+'" data-relationship="'+field.relationshipName+ '" data-step="'+step+'"><td class="chk">';
      filedsHtml += '<div class="checkbox checkbox-primary"><input type="checkbox" '+checked+' value="'+field.relationshipName+'" id="'+chKey+'" disabled="true">';
      filedsHtml += '<label for="'+chKey+'">&nbsp;'+refLabel+'&nbsp;<br /><small>&nbsp;'+field.relationshipName+'</small></label></div>';
      filedsHtml += '</td><td class="type"><a href="#" class="ref-fields-expand-link">'+fieldTypeLabel+'</a>';
      filedsHtml += '<a href="#" class="ref-fields-expand-button"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-chevron-left" viewBox="0 0 16 16">';
        filedsHtml += '<path fill-rule="evenodd" d="M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z"/>';
      filedsHtml += '</svg></a>';
      filedsHtml += '<a href="#" class="ref-fields-close-button"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-chevron-down" viewBox="0 0 16 16">';
        filedsHtml += '<path fill-rule="evenodd" d="M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z"/>';
      filedsHtml += '</svg></a></td></tr>';
    }
    var sObjKey = sobject.name.toUpperCase();
    if(!self.fieldNameMap.hasOwnProperty(sObjKey)) self.fieldNameMap[sObjKey] = {};
    self.fieldNameMap[sObjKey][field.name.toUpperCase()] = field;
    if(field.type == 'reference' && field.relationshipName) {
      if(!self.refShipNameMap.hasOwnProperty(sObjKey)) self.refShipNameMap[sObjKey] = {};
      self.refShipNameMap[sObjKey][field.relationshipName.toUpperCase()] = field;
    }
    if(!self.filedMetaMap.hasOwnProperty(sObjKey)) self.filedMetaMap[sObjKey] = {};
    self.filedMetaMap[sObjKey] = fields;
    if(!self.cacheWordCompleter.hasOwnProperty(sobject.name)) self.cacheWordCompleter[sobject.name] = [];
    self.cacheWordCompleter[sobject.name].push({ name : field.name, label : 'Field' });
  }
  return filedsHtml;
}

// 
SD_SoqlEditor.prototype.requestChildFields = function($targets, step) { 
  var self = this;
  var $panel = jQuery('#nav-field-panel');
  $targets = $targets || $panel.find('tr.ref-row.uncomplete');
  var targetSObjects = [];
  var _insertChildFields = function(sObjName) {
    var sObjKey = sObjName.toUpperCase();
    var fields = self.filedMetaMap[sObjKey];
    //console.log('>>>> _insertChildFields', sObjKey, fields);
    $panel.find('tr.ref-row.processing').each(function() {
      var $tr = jQuery(this);
      var sObjName = $tr.data('sobject');
      var rootrelationship = $tr.data('rootrelationship');
      if(sObjName.toUpperCase() !== sObjKey) return;

      var rootRelationships = [];
      if(!T_Util.isBlank(rootrelationship)) rootRelationships.push(rootrelationship);
      rootRelationships.push($tr.data('relationship'));
      var fieldHtml = self.getFieldRowsHTML({ name : sObjName }, fields, step, rootRelationships.join('.'));
      $tr.removeClass('processing').addClass('completed');
      $tr.find('.checkbox input[type="checkbox"]').removeAttr('disabled');
      $tr.after(fieldHtml);
      // Set ref fields event
      self.resetRefFieldEvent($tr);
    });
  }
  $targets.each(function() {
    var $tr = jQuery(this);
    $tr.removeClass('uncomplete').addClass('processing');
    var sObjName = $tr.data('sobject');
    var sObjKey = sObjName.toUpperCase();
    if(self.filedMetaMap.hasOwnProperty(sObjKey)) {
      _insertChildFields(sObjName, $tr);
      return;
    }
    if(targetSObjects.indexOf(sObjName) < 0) {
      targetSObjects.push(sObjName);
    }
  });

  //console.log('>>> targetSObjects ', targetSObjects);
  if(targetSObjects.length == 0) return;
  self.client.sendRequest({ mode : 'getMultiObjectFieldValues', sobjects : targetSObjects }, function(res) {
    //console.log('>>> getMultiObjectFieldValues callback ', res);
    var metadata = res.metadata || {};
    var sobjects = Object.keys(metadata);
    for(var i = 0; i < sobjects.length; i++) {
      var objMeta = metadata[sobjects[i]];
      var sObjKey = sobjects[i].toUpperCase();
      self.filedMetaMap[sObjKey] = objMeta.fields;
      _insertChildFields(sobjects[i]);  
    }
  });
}

// 
SD_SoqlEditor.prototype.resetRefFieldEvent = function($tr) { 
  var self = this;
  $tr = $tr || jQuery('#nav-field-panel');
  $tr.find('.ref-fields-expand-link').off().click(function(){
    $td = jQuery(this).closest('td');
    $expandBtn = $td.find('.ref-fields-expand-button').first();
    if($expandBtn.is(':visible')) {
      $expandBtn.trigger('click');
    } else {
      $td.find('.ref-fields-close-button').first().trigger('click');
    }
  });
  $tr.find('.ref-fields-expand-button').off().click(function(){
    var $btn = jQuery(this);
    var $tr = $btn.closest('tr.ref-row');
    var pStep = $tr.data('step');
    var targets = [];
    // Loading
    if(!$tr.hasClass('completed')) return;
    $tr.nextAll('.field-row').each(function() {
      if (jQuery(this).is('.step' + pStep)) {
        return false; // next parent reached, stop
      }
      if(jQuery(this).hasClass('step' + (pStep + 1))) {
        jQuery(this).addClass('on');
        targets.push(this);
      }
    });
    $tr.addClass('open');

    // Request once
    if($btn.hasClass('done')) return;
    var nextStep = pStep+2;
    //console.log('>>>>> ref-fields-expand-button step ', pStep, nextStep);
    self.requestChildFields(jQuery(targets).filter('tr.ref-row.uncomplete'), nextStep);
    $btn.addClass('done');
  });
  $tr.find('.ref-fields-close-button').off().click(function(){
    var $btn = jQuery(this);
    var $tr = $btn.closest('tr.ref-row');
    var pStep = $tr.data('step');
    // Loading
    if(!$tr.hasClass('completed')) return;
    $tr.nextAll('.field-row').each(function() {
      if (jQuery(this).is('.step' + pStep)) {
        return false; // next parent reached, stop
      }
      if(jQuery(this).hasClass('step' + (pStep + 1))) {
        jQuery(this).removeClass('on');
      }
      // 
      if(jQuery(this).hasClass('open')) {
        jQuery(this).find('.ref-fields-close-button').trigger('click');
      }
    });
    $tr.removeClass('open');
  });

  // Ref parent field checkbox event
  var $children = self.getChildrenTr($tr);
  $tr.find('input[type="checkbox"]').off().click(function(){
    var allChecked = $(this).is(':checked');
    $children.each(function() {
      var $ctr = jQuery(this);
      if($ctr.hasClass('ref-row')) return;
      $ctr.find('input[type="checkbox"]').prop('checked', allChecked);
    });
    self.refreshSoql(self.editor, self.sObjectName);
  });

  $tr.nextAll('.field-row').each(function() {
    var $ctr = jQuery(this);
    var pStep = $tr.data('step');
    if ($ctr.is('.step' + pStep)) {
      return false; // next parent reached, stop
    }
    if($ctr.hasClass('step' + (pStep + 1))) {
      $ctr.find('input[type="checkbox"]').off().click(function() {
        var checked = jQuery(this).is(':checked');
        if(!checked) {
          $tr.find('input[type="checkbox"]').prop('checked', checked);
        } else {
          var allChecked = true;
          $children.each(function() {
            var $cctr = jQuery(this);
            if($cctr.hasClass('ref-row')) return;
            if(!$cctr.find('input[type="checkbox"]').is(':checked')) {
              allChecked = false;
              return false;
            }
          });
          $tr.find('input[type="checkbox"]').prop('checked', allChecked);
        }
        self.refreshSoql(self.editor, self.sObjectName);
      });
    }
  });
}
// 
SD_SoqlEditor.prototype.getChildrenTr = function($tr) {
  var children = [];
  var currentStep = $tr.data('step');
  var pStep = (currentStep > 0) ? (currentStep - 1) : 0;
  $tr.nextAll('.field-row').each(function() {
    if (jQuery(this).is('.step' + currentStep)) {
      return false; // next parent reached, stop
    }
    if (jQuery(this).is('.step' + pStep)) {
      return false; // next parent reached, stop
    }
    if(jQuery(this).hasClass('step' + (currentStep + 1))) {
      children.push(this);
    }
  });
  return jQuery(children);
}
// SOQL
SD_SoqlEditor.prototype.refreshSoql = function($editor, sObjectName, initCond) {
  var oldSoql = $editor.session.getValue();
  var soqlSplits = oldSoql.split(/ WHERE /i);
  var condition = (T_Util.isBlank(initCond)) ? '' : ' WHERE ' + initCond;
  if(soqlSplits.length > 1) {
    soqlSplits.shift();
    condition = ' WHERE ' + soqlSplits.join(' WHERE ');
  }

  var selectedFields = [];
  $('#nav-field-panel').find('.field-row:not(.ref-row) input[type="checkbox"]').each(function(){
    if($(this).is(':checked')){
      selectedFields.push($(this).val());
    }
  });
  if(selectedFields.length == 0) selectedFields.push('Id');
  var soql = 'SELECT ' + selectedFields.join(', ') + ' FROM ' + sObjectName + condition;
  $editor.session.setValue(soql);
}
/**
 * Query callback and export result to csv
 */
SD_SoqlEditor.prototype.handleQuery = function(options, callback) {
  var self = this;
  var res = options || {};
  var $ele = options.target;
  var currentIndex = res.currentIndex || 0;
  var soql = self.editor.session.getValue();
  var $resultPanel = $('#quick-soql-buttons .query-result').first();
  //var $fieldPanel = $('#nav-field-panel');
  var style = 'success';
  if(T_Util.isBlank(res.done) && T_Util.isBlank(res.totalSize)) {
    style = 'error';
    var message = (typeof res.error == 'object') ? (res.error.name || res.error.errorCode) : res.error;
    $resultPanel.html(message);
    //var panelHeight = $fieldPanel.height();
    //$fieldPanel.css('height', (panelHeight - $resultPanel.height() - 15) + 'px');
    if(callback) callback();
  } else {
    $resultPanel.html('Query Results - Total Rows : <b>' + res.totalSize + '</b>');
    if(res.totalSize == 0) {
      style = 'warning';
      if(callback) return callback();
    }
    var queryObj = self.getQueryMapFromSoql(soql);
    //var soqlObj = self.getObjectFromSoql(soql);
    var soqlObjectName = queryObj.sobject;
    if(T_Util.isBlank(soqlObjectName)) return alert('sObject Name is not found.');
    var isExportExcel = (currentIndex > 1 || $ele.attr('id') == 'btn-quick-soql-export' || $ele.attr('id') == 'btn-quick-soql-export-all');
    // Different from sObject
    if(soqlObjectName.toUpperCase() != self.sObjectName.toUpperCase()) {
      self.loadObjectDescribe(soqlObjectName)
      .then(function(){
        return self.assetRelateMeta(queryObj);
      }).then(function(){
        if(isExportExcel) self.exportToExcel(soqlObjectName, queryObj.fields, res.records, currentIndex, callback);
        // Open query result viewer
        if(!isExportExcel && callback) callback({
          sObjectName : soqlObjectName,
          fields : queryObj.fields,
          records : res.records, 
          childRelShipMap : self.childRelShipMap,
          fieldNameMap : self.fieldNameMap,
          refShipNameMap : self.refShipNameMap,
          refToPairMap : self.refToPairMap,
          metadata : self.metadata
        });
      });
    } else {
      self.assetRelateMeta(queryObj)
      .then(function(){
        // Same with sObject
        if(isExportExcel) self.exportToExcel(self.sObjectName, queryObj.fields, res.records, currentIndex, callback);
        // Open query result viewer
        if(!isExportExcel && callback) callback({
            sObjectName : self.sObjectName,
            fields : queryObj.fields,
            records : res.records, 
            childRelShipMap : self.childRelShipMap,
            fieldNameMap : self.fieldNameMap,
            refShipNameMap : self.refShipNameMap,
            refToPairMap : self.refToPairMap,
            metadata : self.metadata
          });
      });
    }
    //log('>>>> request success ' + JSON.stringify(request));
  }
  $resultPanel.addClass(style);
}

// SOQLObjectField 
SD_SoqlEditor.prototype.getQueryMapFromSoql = function(soql, isSub) {
  var self = this;
  var splitSoql = T_Util.replaceWordFirst(soql, 'select', '__SELECT__');
  var splitSoql = T_Util.replaceWordLast(splitSoql, 'from', '__FROM__');
  var splitSoql = T_Util.replaceBreakLine(splitSoql);
  var sobjectName = self.getObjectFromSoql(splitSoql);
  var queryMap = { fields : [] };
  if(isSub) {
    // Call from subquery
    queryMap['relationshipName'] = sobjectName;
  } else {
    queryMap['sobject'] = sobjectName;
  }

  var matches = splitSoql.match(/\_\_SELECT\_\_.+?\_\_FROM\_\_/gi);
  var fieldText = matches[0];
  fieldText = T_Util.replaceWordFirst(fieldText, '__SELECT__', '');  //.replace(/select/gi,'');
  fieldText = T_Util.replaceWordLast(fieldText, '__FROM__', ''); //.replace(/from/gi,'');
  // Subquery
  var subQuerys = fieldText.match(/\(.+?\)/gi);
  if(subQuerys && subQuerys.length > 0) {
    for(var i = 0; i < subQuerys.length; i++) {
      fieldText = fieldText.replace(subQuerys[i], '__SUBQUERY__');
    }
  }
  var fieldArray = fieldText.split(',');
  var subIndex = 0;
  for(var i=0; i<fieldArray.length; i++){
    var f = fieldArray[i].trim();
    if(f === '__SUBQUERY__') {
      // Subquery
      var subQuery = subQuerys[subIndex];
      subQuery = subQuery.replace('(', '');
      subQuery = subQuery.replace(')', '');
      // 
      var subQueryMap = self.getQueryMapFromSoql(subQuery, true);
      queryMap.fields.push(subQueryMap);
      subIndex++;
      continue;
    }
    var refs = f.split('.');
    if(refs.length == 1) {
      // Reference
      queryMap.fields.push({ name : f });
      //fields.push(f);
    } else {
      queryMap.fields.push({ name : refs });
      //fields.push(refs);
    }
  }
  return queryMap;
}
// SOQLsObject 
SD_SoqlEditor.prototype.getObjectFromSoql = function(soql) {
  var matches = soql.match(/\_\_FROM\_\_\s+([a-zA-Z]+\S*)/gi);
  var sObjectText = matches[0];
  sObjectText = sObjectText.replace(/\_\_FROM\_\_.+?/gi,'');
  return sObjectText.trim();
}


// Load main sObject metadata
// return Promise
SD_SoqlEditor.prototype.loadObjectDescribe = function(target) {
  var self = this;
  // Request sObject fields describe
  return new Promise(function(resolve, reject) {
    self.client.sendRequest({ mode : 'describeSObjects', sObjects : [target] }, function(res) {
      if(!res.describes && !Array.isArray(res.describes)) return resolve();
      var fields = res.describes[0].fields;
      var sObjKey = target.toUpperCase();
      self.fieldNameMap[sObjKey] = {};
      self.refShipNameMap[sObjKey] = {};
      for(var i=0; i < fields.length; i++){
        var field = fields[i];
        self.fieldNameMap[sObjKey][field.name.toUpperCase()] = field;
        if(field.type == 'reference' && field.relationshipName) {
          self.refShipNameMap[sObjKey][field.relationshipName.toUpperCase()] = field;
        }
      }
      // Set childRelationships
      var childRelationships = res.describes[0].childRelationships || [];
      self.childRelShipMap = {};
      for(var i = 0; i < childRelationships.length; i++) {
        var child = childRelationships[i];
        var upperObjectName = child.childSObject.toUpperCase();
        if(!self.upperKeyMetaMap.hasOwnProperty(upperObjectName)) continue;
        var objMeta = self.upperKeyMetaMap[upperObjectName];
        self.childRelShipMap[child.relationshipName.toUpperCase()] = { sobject : child.childSObject, label : objMeta.label };
      }
      return resolve();
    });
  });
  
}
// Param queryObj,  { sobject : 'Account', fields : [ { name : 'Id' }, { relation : 'Child__r', fields : [] } ] }
// refToPairMap, { 'ASSIGNEE' : 'USER', 'CUSTOM__R', 'ACCOUNT' }
// self.fieldNameMap
// return Promise
SD_SoqlEditor.prototype.assetRelateMeta = function(queryObj) {
  var self = this;
  self.refToPairMap = {};
  var fieldArray = queryObj.fields;
  var subQueryObjects = [];
  for(var i = 0; i < fieldArray.length; i++) {
    var field = fieldArray[i];
    if(T_Util.isBlank(field.relationshipName)) continue;
    if(self.childRelShipMap.hasOwnProperty(field.relationshipName.toUpperCase())) {
      var relateSObject = self.childRelShipMap[field.relationshipName.toUpperCase()];
      if(T_Util.isBlank(relateSObject)) continue;
      subQueryObjects.push(relateSObject.sobject);
      queryObj.fields[i]['sobject'] = relateSObject.sobject;
    }
  }
  // Get sobject name need to be cache from fields
  var _getRelateObjectNames = function(sobjectName, fields) {
    var sobjKey = sobjectName.toUpperCase();
    self.refToPairMap[sobjKey] = {};
    var refShipNameMap = self.refShipNameMap[sobjKey] || {}; // referenceTo
    var fieldNameMap = self.fieldNameMap[sobjKey] || {};

    var soqlObjectNames = [];
    for(var i = 0; i < fields.length; i++) {
      var field = fields[i];
      var fieldName = field.name;
      if(!fieldName || !Array.isArray(fieldName)) continue;
      // fieldName : e.g ['RecordType', 'Name']
      for(var f = 0; f < (fieldName.length - 1); f++) {
        var refName = fieldName[f];
        // Custom Object
        if(refName.endsWith('__r')) refName.replace('__r', '__c'); 
        var upperRefName = refName.toUpperCase();
        // Skip non-sObject
        if(!refShipNameMap.hasOwnProperty(upperRefName)) continue;
        var refTo = refShipNameMap[upperRefName].referenceTo;
        refTo = (Array.isArray(refTo)) ? refTo[0] : refTo;
        var upRefTo = refTo.toUpperCase();
        self.refToPairMap[sobjKey][refName.toUpperCase()] = upRefTo;
        // Ignore main sObject which has been cached
        if(fieldNameMap.hasOwnProperty(upRefTo)) continue;
        // Ignore cached or 
        if(soqlObjectNames.indexOf(refTo) >= 0) continue;
        soqlObjectNames.push(refTo);
      }
    }
    return soqlObjectNames;
  }

  // TODO 34
  return new Promise(function(resolve, reject) {
    Promise.resolve()
    .then(function(){
      if(subQueryObjects.length == 0) return true;
      return self.describeSObjects(subQueryObjects);
    })
    .then(function(){
      // describeSObjects requestbug
      // sleep 500ms
      var sleep = (subQueryObjects.length == 0) ? 50 : 500;
      setTimeout(function() {
        // Describe sObject fields in SOQL
        var soqlObjectNames = [];
        // Child Relationship
        for(var i = 0; i < fieldArray.length; i++) {
          var field = fieldArray[i];
          var fieldName = field.name;
          if(T_Util.isBlank(fieldName) && T_Util.isNotBlank(field.sobject)) {
            var relObjectNames = _getRelateObjectNames(field.sobject, field.fields);
            soqlObjectNames = T_Util.concatWithoutDuplicate(soqlObjectNames, relObjectNames);
            continue;
          }
        }
        // Reference Field
        var refObjectNames = _getRelateObjectNames(queryObj.sobject, fieldArray);
        soqlObjectNames = T_Util.concatWithoutDuplicate(soqlObjectNames, refObjectNames);
        // Splice has cached object name
        for(var i = 0; i < soqlObjectNames.length; i++) {
          var objectName = soqlObjectNames[i];
          if(self.fieldNameMap.hasOwnProperty(objectName.toUpperCase())) {
            soqlObjectNames.splice(i, 1);
          }
        }
        if(soqlObjectNames.length > 0) {
          // Describe sObjects
          self.describeSObjects(soqlObjectNames).then(function(){ return resolve(); });
        } else {
          return resolve();
        }
      }, sleep);
    });
  });
}


// ExportToExcel action
SD_SoqlEditor.prototype.exportToExcel = function(sObjectName, fields, records, startIndex, callback) {
  var self = this;
  //var refShipNameMap = self.refShipNameMap; // referenceTo
  var styleBorder = { top: { style: "thin" }, left: { style: "thin" }, bottom: { style: "thin" }, right: { style: "thin" }};
  var fontSize = 12;
  //var workbook;
  XlsxPopulate
  .fromBlankAsync()
  .then(function(xworkbook) {
    // Create sheets
    var sheet = xworkbook.sheet(0);
    if(!Array.isArray(records)) records = [records];
    // Header
    var styles = { 'border' : styleBorder, 'fontSize' : fontSize, 'verticalAlignment' : 'center', 'fill' : '3367d6', 'fontColor' : 'ffffff'};
    for(var i = 0; i < fields.length; i++) {
      var field = fields[i];
      var fieldName = field.name;
      var label = '', fieldApiName = '';
      // sub query
      if(fieldName == null && field.relationshipName) {
        var childRelShip = self.childRelShipMap[field.relationshipName.toUpperCase()];
        if(childRelShip) {
          label = childRelShip.label;
          fieldApiName = field.relationshipName;
        }
      } else {
        fieldApiName = (Array.isArray(fieldName)) ? fieldName.join('.') : fieldName;
        var fDesc = self.getFieldMeta(sObjectName, fieldName);
        label = (fDesc) ? fDesc.label : '';
      }
      var cellVal1 = label;

      var columnNo = T_Util.getColumn(i+1); // A
      // Row 1 : Label
      var row1 = sheet.row(1); // Row 1~
      row1.height(22.5);
      var cell1 = sheet.cell(columnNo + '1');
      
      cell1.value(cellVal1).style(styles);
      // Row 2 : API Name
      var row2 = sheet.row(2); // Row 1~
      row2.height(22.5);
      var cell2 = sheet.cell(columnNo + '2');
      cell2.value(fieldApiName).style(styles).style('fill', '8ea9db');
    }
    // Record body
    var cellStyles = { 'border' : styleBorder, 'fontSize' : fontSize, 'verticalAlignment' : 'center'};
    for(var i = 0; i < records.length; i++) {
      var rowNo = i + 2;
      var row = records[i];
      var upperRow = T_Util.upperProperty(row);
      if(T_Util.isBlank(sObjectName)) sObjectName = row.attributes.type;
      for(var h = 0; h < fields.length; h++) {
        var columnNo = T_Util.getColumn(h+1); // A
        var cell = sheet.cell(columnNo + (rowNo + 1));
        var field = fields[h];
        var fieldName = field.name;
        // sub query
        if(fieldName == null && field.relationshipName) {
          var valObj = upperRow.hasOwnProperty(field.relationshipName.toUpperCase()) ? 
                    upperRow[field.relationshipName.toUpperCase()] : {};
          var childRecords = (valObj) ? valObj.RECORDS : null;
          childRecords = T_Util.clearAttributes(childRecords);
          cell.value(JSON.stringify(childRecords)).style(cellStyles);
        } else {
          var valObj = self.getObjectValue(fieldName, upperRow);
          var value = (typeof valObj == 'object' && !T_Util.isBlank(valObj)) ? valObj.value : valObj;
          // Relation
          cell.value(value).style(cellStyles);
        }
        //var fieldName = fields[h];
      }
    }
    // Rejust cell width to fit data
    for(var c = 0; c < fields.length; c++) {
      var cellChar = T_Util.getColumn(c+1); // A
      // A1:A20
      var maxLength = sheet.range(cellChar + '1:' + cellChar + (records.length + 2))
      .reduce(function(max, cell) {
        var value = cell.value();
        if (value === undefined) return max;
        if (T_Util.isBlank(value)) return max;
        // set length to 2x if has japanese string
        var vLen = T_Util.getBytes(value.toString());
        return Math.min(Math.max(max, vLen), 100);
      }, 0);
      sheet.column(cellChar).width(maxLength + 5);
    }
    return xworkbook.outputAsync();
  })
  .then(function(blob) {
    var link = document.createElement('A');
    //var blob = new Blob([s2ab(fileContent)],{type:""})
    link.href = window.URL.createObjectURL(blob);
    var fileName = sObjectName + '-';
    if(startIndex == 0) {
      // 1
      fileName += records.length + '.xlsx';
    } else {
      fileName += (startIndex+1) + '_' + (startIndex+records.length) + '.xlsx';
    }
    link.download = fileName;
    link.target = '_blank';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);

    if(callback) callback();
  });
}

// keyObject 
// return { value : '', attributes : { type : 'User', url : '/services/data/v42.0/sobjects/User/xxxx'} }
SD_SoqlEditor.prototype.getObjectValue = function(fields, obj) {
  var self = this;
  if(T_Util.isBlank(obj)) return null;
  if(typeof fields == 'string') fields = [fields];
  var newFields = Array.from(fields);
  var key = newFields[0].toUpperCase();
  if(!obj.hasOwnProperty(key)) {
    return null;
  }
  if(typeof obj[key] == 'object' && !T_Util.isBlank(obj[key])) {
    if(!obj[key].hasOwnProperty('ATTRIBUTES')) {
      // Relationobject
      return JSON.stringify(obj[key]).replace(/"/g, "'");
    }
    // 
    newFields.splice(0, 1);
    var upperRow = T_Util.upperProperty(obj[key]);
    return self.getObjectValue(newFields, upperRow);
  } else {
    return { value : obj[key], attributes : obj['ATTRIBUTES'] };
  }
}

/** Init Editor */
SD_SoqlEditor.prototype.initEditor = function(ele, mode) {
  //var ele = this;
  if(typeof ace == 'undefined') return;
  mode = mode || 'java';
  var editor = ace.edit(ele);
  editor.setTheme("ace/theme/chrome");
  editor.getSession().setMode('ace/mode/' + mode);
  editor.setOptions({
    maxLines: Infinity,
    enableBasicAutocompletion: true,
    enableLiveAutocompletion: true
  });
  if(mode == 'sql'){
    // SOQL 
    editor.getSession().setUseWrapMode(true);
  } else {
    editor.setReadOnly(true);
  }
  
  editor.renderer.setShowGutter(false);
  editor.$blockScrolling = Infinity;
  
  return editor;
}
/** Format object map with uppercase name key */
SD_SoqlEditor.prototype.getUpperKeyMap = function(sObjects) {
  var keyMap = {};
  if(!Array.isArray(sObjects)) return keyMap;
  for(var i = 0; i < sObjects.length; i++) {
    var sobj = sObjects[i];
    keyMap[sobj.name.toUpperCase()] = sobj;
  }
  return keyMap;
}

/** Describe fields of sobjects */
SD_SoqlEditor.prototype.describeSObjects = function(sObjects) {
  var self = this;
  return new Promise(function(resolve, reject) {
    var reqObjectNames = [];
    for(var i = 0; i < sObjects.length; i++) {
      var upperObjName = sObjects[i].toUpperCase();
      if(self.fieldNameMap.hasOwnProperty(upperObjName)) continue;
      reqObjectNames.push(sObjects[i]);
    }
    if(reqObjectNames.length == 0) return resolve();
    self.client.sendRequest({ mode : 'describeSObjects', sObjects : sObjects }, function(res) {
      if(!res.describes && !Array.isArray(res.describes)) return resolve();
      for(var i=0; i < res.describes.length; i++){
        var desc = res.describes[i];
        var sObjKey = desc.name.toUpperCase();
        self.fieldNameMap[sObjKey] = {};
        self.refShipNameMap[sObjKey] = {};
        for(var f=0; f < desc.fields.length; f++){
          var field = desc.fields[f];
          self.fieldNameMap[sObjKey][field.name.toUpperCase()] = field;
          if(field.type == 'reference' && field.relationshipName) {
            self.refShipNameMap[sObjKey][field.relationshipName.toUpperCase()] = field;
          }
        }
      }
      return resolve();
    });
  });
}

/** Metadata CacheFieldLabel
 * return { label : '', describe : fieldDescribe }
*/
SD_SoqlEditor.prototype.getFieldMeta = function(sObjectName, field) {
  var self = this;
  var mainFieldMap = self.fieldNameMap[sObjectName.toUpperCase()];
  var refToPairChildMap = self.refToPairMap[sObjectName.toUpperCase()];
  if(Array.isArray(field)) {
    var fields = Array.from(field);
    var defaultLabel = field.join('.');
    // Array, e.g ['Ref__r', 'Name']
    var fieldName = fields.pop();
    fieldName = fieldName.toUpperCase();
    
    // Convert Object Label
    var objLables = [];
    var refApiName, refObjName;
    for(var i = 0; i < fields.length; i++) {
      refApiName = fields[i].toUpperCase();
      if(!refToPairChildMap.hasOwnProperty(refApiName)) return { label : defaultLabel };
      refObjName = refToPairChildMap[refApiName];
      // Replace custom__r to custom__c or Account to AccountID
      var subRefName = (refApiName.endsWith('__R')) ? refApiName.replace('__R', '__C') : refApiName + 'ID';
      if(mainFieldMap.hasOwnProperty(refApiName)) {
        // REFERENCE__R || ACCOUNT
        objLables.push(mainFieldMap[refApiName].label);
        mainFieldMap = self.fieldNameMap[refObjName.toUpperCase()] || {};
      } else if(mainFieldMap.hasOwnProperty(subRefName)) {
        // PARENTID || RECORDTYPEID
        var subRefLabel = mainFieldMap[subRefName].label;
        if(subRefLabel.endsWith(' ID')) subRefLabel = T_Util.replaceLast(subRefLabel, ' ID', '');
        objLables.push(subRefLabel);
        mainFieldMap = self.fieldNameMap[refObjName.toUpperCase] || {};
      }
    }

    var fieldLabel = '';
    var upperRefObjName = refObjName.toUpperCase();
    if(!self.fieldNameMap.hasOwnProperty(upperRefObjName)) return { label : defaultLabel };
    var refFieldMap = self.fieldNameMap[upperRefObjName];
    if(refFieldMap.hasOwnProperty(fieldName.toUpperCase())) {
      fieldLabel = refFieldMap[fieldName.toUpperCase()].label;
    }
    return (objLables.length > 0 && fieldLabel != '') ? 
          { label : objLables.join('.') + '.' + fieldLabel, describe : refFieldMap[fieldName.toUpperCase()] } : { label : defaultLabel };
  } else {
    // String, e.g Name
    var upperName = field.toUpperCase();
    if(mainFieldMap.hasOwnProperty(upperName)) {
      var fdesc = mainFieldMap[upperName];
      return { label : fdesc.label, describe : fdesc };
    }
    return  { label : field };
  }
}
// Clear attributes property from records
SD_SoqlEditor.prototype.clearAttributes = function(records) {
  if(!records || records.length == 0) return records;
  var newRecords = [];
  var keys = Object.keys(records[0]);
  for(var r = 0; r < records.length; r++) {
    var map = {};
    var record = records[r];
    for(var i=0; i<keys.length; i++) {
      var key = keys[i];
      if(key == 'attributes' || key == 'ATTRIBUTES') continue;
      map[key] = record[key];
    }
    newRecords.push(map);
  }
  return newRecords;
}